
.profile-wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 200px;
  height: 100%;
  box-sizing: border-box;
  padding: 40px 10px 10px 10px;
  background: #ffffff;
  border-right: 1px solid #E6E6E6;
  word-break: break-all;
  overflow: auto;
  z-index: 10;
}
.chat-setting-wrap {
  position: fixed;
  top: 0;
  left: 200px;
  width: 200px;
  height: 100%;
  box-sizing: border-box;
  padding: 40px 0 0 0;
  background: #ffffff;
  border-right: 1px solid #E6E6E6;
  word-break: break-all;
  z-index: 10;
}
.albums {
  position: fixed;
  top: 0;
  left: 400px;
  width: 200px;
  height: 100%;
  box-sizing: border-box;
  padding: 40px 0 0 0;
  background-color: #ffffff;
  border-right: 1px solid #e6e6e6;
  z-index: 10;
  overflow: auto;
}
.albums .how-to-use-album {
    width: 100%;
    padding: 10px;
    background-color: #009EE7;
    color: #ffffff;
    box-sizing: border-box;
    font-size: 12px;
    word-break: keep-all;
    text-align: center;
    line-height: 1.6;
}
.albums .how-to-use-album .code {
      background-color: #ffffff;
      color: #000000;
      border-radius: 99px;
      padding: 0 5px;
}
.albums .drop-here {
    padding: 100px 10px;
    box-sizing: border-box;
    text-align: center;
}
.albums .pic {
    padding: 10px;
    text-align: center;
}
.albums .pic img {
      margin-bottom: 5px;
      max-width: 180px;
}
.chats {
  margin-top: 40px;
  margin-left: 600px;
}
.dev div {
  margin: 20px 0;
}
h2 {
  font-weight: bold;
  font-size: 16px;
  margin: 20px 0;
}
#chat-render {
  position: absolute;
  left: 200%;
  top: 0;
}
#chat-render .chat-preview {
    zoom: 1 !important;
}



